:root {
  --card-width: 400px;      
  --container-width: 640px; 
  --carousel-height: 240px;
}

.cardsWrapper {
  position: relative;
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

.cardContainer {
  position: relative;
  width: var(--container-width);
  min-width: var(--container-width);
  max-width: 100%;
  min-height: 600px;
  background: transparent;
  border-radius: 12px;
  overflow: visible;
  border: none;
  box-shadow: none;
}

.cardTitle {
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
  letter-spacing: 0.3px;
}

.cardContainer > .mantine-Stack-root,
.cardContainer > .mantine-Group-root {
  padding: 16px;
}

.bankCard {
  width: var(--card-width);
  border-radius: 16px;
  padding: 24px;
  color: white;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 24px rgba(102, 126, 234, 0.35);
  transition: transform 0.25s ease;
  min-height: 200px;
  user-select: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
}

.cardPattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    repeating-linear-gradient(45deg, rgba(255,255,255,0.03) 0px 2px, transparent 2px 8px),
    repeating-linear-gradient(-45deg, rgba(255,255,255,0.02) 0px 1px, transparent 1px 6px),
    radial-gradient(circle at 15% 25%, rgba(255,255,255,0.08) 0px 2px, transparent 2px),
    radial-gradient(circle at 85% 75%, rgba(255,255,255,0.06) 0px 1px, transparent 1px);
  pointer-events: none;
  border-radius: 16px;
  z-index: 1;
}

.cardGeometricPattern {
  position: absolute;
  top: -50%;
  right: -30%;
  width: 200%;
  height: 200%;
  background: 
    radial-gradient(circle at 30% 30%, rgba(255,255,255,0.1) 0%, transparent 50%),
    conic-gradient(from 0deg at 50% 50%, transparent 0deg, rgba(255,255,255,0.05) 90deg, transparent 180deg);
  pointer-events: none;
  z-index: 1;
  border-radius: 16px;
}

.bankCard::before {
  content: '';
  position: absolute;
  top: -40%;
  right: -40%;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.12) 0%, transparent 70%);
  pointer-events: none;
  z-index: 2;
  border-radius: 16px;
}

.chip {
  width: 80px;
  height: auto;
  object-fit: contain;
  position: absolute;
  top: -28px;
  right: -10px;
  z-index: 3;
  user-select: none;
  pointer-events: none;
}

.cardDecorativeLines {
  position: absolute;
  bottom: 20px;
  left: 24px;
  right: 24px;
  height: 1px;
  background: linear-gradient(90deg, transparent 0%, rgba(255,255,255,0.3) 20%, rgba(255,255,255,0.3) 80%, transparent 100%);
  z-index: 2;
}

.cardTop {
  margin-bottom: 18px;
  position: relative;
  z-index: 3;
}

.cardBank {
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 6px;
}

.cardType {
  font-size: 0.85rem;
  opacity: 0.9;
  font-weight: 500;
}

.cardMiddle {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 3;
  padding-right: 70px;
}

.cardNumber {
  font-size: 1.3rem;
  font-weight: 600;
  letter-spacing: 2px;
  font-family: 'Courier New', monospace;
}

.cardBottom {
  margin-top: auto;
  position: relative;
  z-index: 3;
}

.cardLabel {
  font-size: 0.7rem;
  opacity: 0.8;
  margin-bottom: 4px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.cardValue {
  font-size: 0.9rem;
  font-weight: 600;
  letter-spacing: 1px;
}

.carouselContainer {
  position: relative;
  width: 100%;
  height: var(--carousel-height);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: visible;
  margin-bottom: 50px;
}

.carouselContainer .swiper {
  width: 100%;
  height: 100%;
}

.carouselContainer .swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  touch-action: pan-y;
}

.swiper-button-prev,
.swiper-button-next {
  color: rgba(0, 0, 0, 0.75);
  background: rgba(255, 255, 255, 0.95);
  border-radius: 8px;
  padding: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.swiper-button-prev {
  left: calc(50% - (var(--card-width) / 2) - 12px);
}

.swiper-button-next {
  right: calc(50% - (var(--card-width) / 2) - 12px);
}

.swiper-pagination {
  position: absolute !important;
  bottom: -60px !important;
  left: 50% !important;
  transform: translateX(-50%) !important;
  margin-top: 0 !important;
  width: auto !important;
}

.swiper-pagination-bullet {
  background: rgba(0, 0, 0, 0.25);
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background: #2563eb;
}

@media (max-width: 768px) {
  :root {
    --container-width: 100%;
    --card-width: 340px;
  }

  .cardsWrapper {
    margin-left: 0;
    padding: 10px;
    justify-content: center;
  }

  .swiper-button-prev {
    left: 8px;
  }

  .swiper-button-next {
    right: 8px;
  }

  .cardContainer {
    min-height: 560px;
  }
}
